import * as XLSX from 'xlsx';

const BATCH_SIZE = 1000; // Configurable batch size

export class ExcelProcessingService {
  async processExcelFile(file) {
    console.log('üìù Starting to process file:', file.name);
    
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          console.log('üîç Reading Excel file...');
          const workbook = XLSX.read(e.target.result, { type: 'binary' });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const data = XLSX.utils.sheet_to_json(worksheet);
          
          console.log(`üìä Found ${data.length} rows in Excel`);
          console.log('üìã First row sample:', data[0]);
          
          const batches = this.createBatches(data);
          console.log(`üì¶ Created ${batches.length} batches`);
          
          resolve(batches);
        } catch (error) {
          console.error('‚ùå Error processing Excel:', error);
          reject(error);
        }
      };

      reader.onerror = () => {
        console.error('‚ùå Failed to read file');
        reject(new Error('File reading failed'));
      };

      reader.readAsBinaryString(file);
    });
  }

  createBatches(data) {
    console.log('üîÑ Starting batch creation');
    const batches = [];
    
    for (let i = 0; i < data.length; i += BATCH_SIZE) {
      const batch = data.slice(i, i + BATCH_SIZE);
      console.log(`üì¶ Creating batch ${batches.length + 1} with ${batch.length} items`);
      
      const transformedBatch = this.transformBatchToPayload(batch);
      batches.push(transformedBatch);
    }
    
    console.log(`‚úÖ Successfully created ${batches.length} batches`);
    return batches;
  }

  transformBatchToPayload(batch) {
    console.log(`üîÑ Transforming batch of ${batch.length} items`);
    
    const payload = {
      items: batch.map(row => ({
        type: "VPMReference",
        attributes: {
          title: row.Title || '',
          description: row.Description || '',
          "dseno:EnterpriseAttributes": {
            "XP_VPMReference_Ext.EMR_ERP_PrimaryUOM": row["Unit Of Measure"] || ''
          },
          "dseng:EnterpriseReference": {
            partNumber: row["EIN Number"] || ''
          }
        },
        classificationType: row["Type"] || "Standard",
        collabspace: row["Collaborative Space"] || ''
      })),
      background: false,
      emailNotification: false,
      securityContext: `VPLMProjectLeader.0000000001.${batch[0]["Collaborative Space"] || ''}`,
      userId: "emrserviceuser"
    };

    console.log('‚úÖ Batch transformation complete', {
      batchSize: payload.items.length,
      sampleItem: payload.items[0]
    });

    return payload;
  }
}